.container {
  height: 600px;
  display: flex;
  flex-direction: column;
  background-color: aquamarine;
  justify-content: center;
}

.galerry {
  width: 1400px;
  height: 300px;
  margin-top: 0px;
  display: flex;
  flex-direction: row;
  gap: 10px;
}
.img {
  width: 200px;
  height: 200px;
  border-radius: 50px;
}
.center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.price {
  color: blue;
  font-size: 30px;
  font-weight: 600;
  animation-name: example;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes example {
  0% {
    color: red;
  }
  25% {
    color: yellow;
  }
  50% {
    color: blue;
  }
  75% {
    color: green;
  }
  100% {
    color: red;
  }
}
.ketqua {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 0;
}
.priceketqua {
  color: rgb(163, 1, 1);
  font-size: 50px;
  font-weight: 700;
  margin-left: 60px;
}
.imgketqua {
  width: 250px;
  height: 220px;
  border-radius: 15px;
}
.ketquacontainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 100px;
}

.btn {
  width: 200px;
  outline: none;
  border: none;
  height: 50px;
  background-color: #4747f8;
  border-radius: 10px;
  margin-top: 50px;
  font-size: 20px;
  color: wheat;
}
.img7 {
  height: 220px;
  width: 250px;
  border-radius: 20px;
  margin-top: 0;
}
.dog {
  width: 220px;
  height: 220px;
}
.cat {
  width: 220px;
  height: 220px;
}
